.container-eveniment {
    height: 100vh;
    display: grid;
    gap: 100px;
    grid-template-columns: 820px 820px;
    grid-template-rows: 500px 500px 500px 500px 500px 500px 500px 500px 500px 500px 500px;
    justify-content: center;
  }

  .item-eveniment {
    font-size: 5rem;
    font-family: Arial, Helvetica, sans-serif;
    /* TODO: Use Flexbox to align the text to the center horizontally and vertically. 
    See goal1 image.
    */

    display: flex;
    justify-content: center;
    align-items: center;
  }

  .titlu-evenimente {
    font-size: 18px;
    padding-top: 110px;
    padding-bottom: 30px;
    margin-left: 80px;
    display: flex;
    justify-content: flex-start;
    color: #fff;
  }

.img-eveniment {
    width: 820px;
    height: 500px;
  }

.img2-eveniment {
    height: 1100px;
    width: 820px;
  }

.biserica {
grid-row: span 2;
}

.mati {
grid-row: span 2;
}

.dans {
grid-row: span 2;
}

.nasa {
grid-row: span 2;
}

.tort {
grid-row: span 2;
}

.mireasa {
grid-row: span 2;
}

.cursor {
cursor: pointer;
}

.demo {
opacity: 1;
}

.active,
.demo:hover {
opacity: 0.75;
}

.next-evenimente {
  display: flex;
  justify-content: flex-end;
  margin-top: 295%;
  padding-right: 200px;
}

.arrow {
  height: 40px;
  position: absolute;
  margin-left: 15px;
  margin-top: 4px;
}

.button-evenimente {
  font-size: 30px;
  font-family: 'Poppins', sans-serif;
}

.link {
background-image: linear-gradient(
  to right,
  #FFC94A,
  #FFC94A 50%,
  #fff 50%
);
background-size: 200% 100%;
background-position: -100%;
display: inline-block;
padding: 5px 0;
position: relative;
background-clip: text;
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
transition: all 0.3s ease-in-out;
}

.link:hover {
background-position: 0;
}

p {
  color: #fff;
  font-size: 22px;
  font-family: 'Poppins', sans-serif;
}

.culoare-evenimente {
  background-color: black;
}
