.container-sedinta {
    height: 100vh;
    display: grid;
    gap: 100px;
    grid-template-columns: 820px 820px;
    grid-template-rows: 500px 500px 500px 500px 500px 500px 500px 500px 500px 500px 500px 500px 500px;
    justify-content: center;
  }

  .item-sedinta {
    font-size: 5rem;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    /* TODO: Use Flexbox to align the text to the center horizontally and vertically. 
    See goal1 image.
    */

    display: flex;
    justify-content: center;
    align-items: center;
  }

  .titlu-sedinta {
    font-size: 18px;
    padding-top: 110px;
    padding-bottom: 30px;
    margin-left: 80px;
    display: flex;
    justify-content: flex-start;
    color: #fff;
  
  }

  .img-sedinta {
    width: 820px;
    height: 500px;
  }

.img2-sedinta {
    height: 1100px;
    width: 820px;
  }

